@import "../styles/scss/mixins";
@import "../styles/scss/variables";

.budgetList {
  @include spaceY(20px);
  color: $textColor;
  padding: 15px 5px;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 370px));
  justify-items: center;
  align-items: flex-start;
  justify-content: center;
}
.budgetBox {
  @include spaceY(10px);
  margin: 0 auto;
  width: 100%;
  max-width: 370px;
  padding-bottom: 15px;
  border-bottom: 1px solid $grayColor;
}
.budgetDetails {
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-weight: 600;
  .expenseTotal {
    color: $redColor;
  }
  .budgetAmount {
    color: green;
  }
}
.progressBar {
  width: 100%;
  height: 15px;
  border-radius: 3px;
  border: 1px solid $primaryColor;
}
.progressBar::-webkit-progress-bar {
  background-color: transparent;
}
.progressBar::-webkit-progress-value {
  background-color: $primaryColor;
}
.progressBar.fullProgress {
  border: 1px solid $redColor;
}
.progressBar.fullProgress::-webkit-progress-value {
  background-color: $redColor;
}

.BtnBox {
  @include spaceX(5px);
  justify-content: flex-end;
  align-items: center;

  .addBtn {
    @include btnStyle(transparent, $primaryColor, $grayColor, 5px);
  }
  .viewBtn {
    @include btnStyle(transparent, #bbb, $grayColor, 5px);
  }
  .deleteBtn {
    @include btnStyle(transparent, #bbb, transparent, 5px);
    transition:all .3s ease-out;
    &:hover{
      color: $redColor;
    }
  }
}

.viewExpense {
  padding: 0 20px;
}
.viewBox {
  &:not(:last-child) {
    padding-bottom: 10px;
  }
  div {
    @include spaceX(10px);
    justify-content: space-between;
    align-items: flex-start;
    font-weight: 600;
  }
  .date {
    font-size: 14px;
    opacity: 0.5;
  }
  .iconBtn {
    border: none;
    padding: 2px;
    color: $redColor;
    background-color: transparent;
    display: flex;
    cursor: pointer;
  }
}
